// @import "reset.scss";
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap');

body {
	padding: 0;
	margin: 0;
}

ol {

	li {
		border-bottom: 1px solid black;
		list-style: none;

		&:last-child {
			border: none;
		}
	}
}

* {
	box-sizing: border-box;
	transition: all .2s ease-out;
	font-family: 'Roboto', sans-serif;
	--main-bg-color: #4274e9;
	--main-bg-color2: #153D97;

	ol {
		margin: 0;
		padding: 0;
		max-width: 1000px;
	}

	img {
		max-width: 100%;
	}
}

p {
	padding: 0;
	margin: 0;
}

.tableOfList__link {
	&:hover {
		cursor: pointer;
	}
}

.main {
	text-align: left;
	margin: 0 auto;
	width: 100%;
}

.article {
	margin: 10px;

	&__title {
		// color: #fff;
		border-bottom: 1px solid black;
		font-weight: 500;
		font-size: 1.7rem;
		font-family: 'Roboto', sans-serif;
		padding: 1.5rem;

		&:hover {

			padding: 1.5rem;
		}
	}

	&__div {
		border-radius: 0 0 20px 20px;
		margin-bottom: 1rem;
		display: flex;
		justify-content: flex-start;
		align-items: flex-start;
		@media screen and (min-width: 700px) {
			justify-content: flex-start;
		}
	}

	&__subtitle {
		.name {
			display: block;
			text-decoration: underline;
		}

		input {
			outline: none;
			border: none;
			font-size: 1.3rem;
			width: 200px;
		}

		.sublist {
			padding-left: 2rem;
		}

		font-size: 1.3rem;
		font-family: 'Roboto', sans-serif;
		padding: 0.5rem 0.5rem 0.5rem 0rem;
		margin-left: 1rem;
		line-height: 2.5rem;
		letter-spacing: 0.03rem;

		.bold {
			font-weight: 500;
			word-wrap: break-word;
		}

		ol, ul {
			font-family: 'Roboto', sans-serif;


			li {
				font-size: 1rem;
				font-family: 'Roboto', sans-serif;
				padding: 1rem 0.5rem 1rem 0;
				letter-spacing: 0.03rem;
				line-height: 1.5rem;
			}
		}

		p {
			font-weight: 500;
		}
	}

	@media screen and (max-width: 890px) {
		margin: 0;
		padding: 0;
	}

}

// below table-of-list styles

.wrapper {
	display: flex;
	max-width: 1400px;
	margin: 0 auto;
}

.table-of-list {
	min-width: 300px;
	margin: 0;
	box-sizing: border-box;

	ul {
		position: sticky;
		top: 100px;
		margin: 0;
		padding: 0 0 0 20px;
		list-style: none;

		li {
			text-transform: lowercase;
			margin: 20px;
			padding-left: 10px;

			&:hover {
				border-left: 1px solid var(--main-bg-color);
			}

			div:hover {
				color: var(--main-bg-color);
			}
		}
	}
}

div:target > div {
	animation: highlight .8s ease-out;
}

@keyframes highlight {

	0% {
		background-color: #FFFF66;
	}

	100% {
		background-color: #FFFFFF;
	}

}


#ssz-check,
#unloading,
#unloading__from-external,
#unloading__storno,
#unloading__asutl,
#unloading__zhd-request,
#unloading__activate,
#unloading__create-new-task,
#unloading__find-sheets,
#unloading__move-sheets,
#unloading__letter-report,
#unloading__ex-filter,
#unloading__auto-unloading,
#unloading__sklad-3037,
#unloading__issuance-of-cards,
#unloading__order-of-sheets,
#unloading__sheet-order,
#unloading__unloading-sklad-l027 {
	border-top: 80px solid transparent;
	margin-top: -80px;
	padding: 15px;
	/* паддинги - без проблем */
	-moz-background-clip: padding;
	-webkit-background-clip: padding-box;
	background-clip: padding-box;
	@media screen and (max-width: 890px) {
		margin: -80px 0 0 0;
		padding: 0;
	}
}


@media screen and (max-width: 425px) {
	.table-of-list {
		display: block;
		position: fixed;
		// visibility: hidden;
		top: 0;
		right: -80%;
		width: 300px;
		height: 100%;
		margin: 0;
		padding: 80px 0;
		list-style: none;
		text-align: center;
		background-color: #ECEFF1;
		box-shadow: 1px 0 6px rgba(0, 0, 0, .2);
	}
	.main {
		max-width: 425px;
	}
	.article {
		padding-right: 0;

		&__title {
			font-size: 1.3rem;
		}

		&__subtitle {
			font-size: 1rem;
		}
	}
	.menu__box {
		display: block;
		position: fixed;
		visibility: hidden;
		top: 0;
		left: -100%;
		width: 300px;
		height: 100%;
		margin: 0;
		padding: 80px 0;
		list-style: none;
		text-align: center;
		background-color: #ECEFF1;
		box-shadow: 1px 0 6px rgba(0, 0, 0, .2);
	}

	/* элементы меню */
	.menu__item {
		display: block;
		padding: 12px 24px;
		color: #333;
		font-family: 'Roboto', sans-serif;
		font-size: 20px;
		font-weight: 600;
		text-decoration: none;
	}

	.menu__item:hover {
		background-color: #CFD8DC;
	}
	#menu__toggle:checked ~ .menu__btn > span {
		transform: rotate(45deg);
	}

	#menu__toggle:checked ~ .menu__btn > span::before {
		top: 0;
		transform: rotate(0);
	}

	#menu__toggle:checked ~ .menu__btn > span::after {
		top: 0;
		transform: rotate(90deg);
	}

	#menu__toggle:checked ~ .menu__box {
		visibility: visible;
		left: 0;
	}
}

@media screen and (max-width: 890px) {
	.wrapper {
		flex-direction: column;
	}
	.table-of-list {
		display: block;
		position: fixed;
		visibility: hidden;
		top: 0;
		right: -100%;
		width: 300px;
		height: 100%;
		margin: 0;
		padding: 80px 0;
		list-style: none;
		text-align: center;
		background-color: #60cfff;
		box-shadow: 1px 0px 6px #60cfff;

		.tableOfList__link {
			color: white;
		}
	}

	.article {
		padding-right: 0;
	}
}

@media print {
	header, .table-of-list {
		display: none;
	}
	.sublist {
		max-height: none;
		overflow: auto;
		transition: all .5s ease-out;
	}
}

@media screen and (max-width: 700px) {
	ol {
		max-width: 100%;
	}
	* {
		img {
			display: inline-block;
			//width: 100vw;
		}
	}
}